.inputbox:focus,
.textarea:focus {
    border-color: rgba(6, 31, 92, 0.8);
    outline: 0;
    outline: thin dotted \9;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(199, 52, 67, 0.6);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(199, 52, 67, 0.6);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(199, 52, 67, 0.6);
}

.inputbox,
.textarea {
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border linear 0.2s, box-shadow linear 0.2s;
    -moz-transition: border linear 0.2s, box-shadow linear 0.2s;
    -o-transition: border linear 0.2s, box-shadow linear 0.2s;
    transition: border linear 0.2s, box-shadow linear 0.2s;
}


/* global */

.g-hidden {
    display: none;
}

.g-line {
    zoom: 1;
}

.g-line:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}


/* modal */

.b-modal {
    position: relative;
    margin: 0 auto;
    max-width: 450px;
    width: 100%;
}

.b-modal_close {
    background: url(../img/modal_form_close.png) no-repeat top center;
    width: 50px;
    height: 50px;
    cursor: pointer;
    z-index: 400;
    position: absolute;
    top: 0;
    right: 0;
}

.b-modal_close:hover {
    background-position: center bottom;
}

.b-modal {
    position: relative;
    z-index: 990;
}


/* modal */

.arcticmodal-overlay,
.arcticmodal-container {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 11000;
}

.arcticmodal-container {
    overflow: auto;
    margin: 0;
    padding: 0;
    border: 0;
    border-collapse: collapse;
}

*:first-child+html .arcticmodal-container {
    height: 100%
}

.arcticmodal-container_i {
    height: 100%;
    margin: 0 auto;
    width: 100%;
}

.arcticmodal-container_i2 {
    padding: 24px;
    margin: 0;
    border: 0;
    vertical-align: middle;
}

.arcticmodal-error {
    padding: 20px;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
    -khtml-border-radius: 10px;
    border-radius: 10px;
    background: #000;
    color: #fff;
}

.fancy-bg {
    display: none;
}

#fancybox-outer {
    background: none;
}

#fancybox-left,
#fancybox-right {
    background: url(../img/detail_arrow.png) no-repeat;
    width: 23px;
    height: 42px;
    cursor: pointer;
    z-index: 4000;
    top: 50%;
    margin-top: -21px;
    left: -36px;
}

#fancybox-right {
    left: auto;
    right: -36px;
    transform: rotate(180deg);
}

#fancybox-close {
    background: url(../img/fancy_close.png) no-repeat;
    width: 31px;
    height: 30px;
    cursor: pointer;
    position: absolute;
    display: block;
    right: 10px;
    top: -35px;
    transition: 0.4s;
}

#fancybox-close:hover {
    -moz-transform: rotate(360deg);
    -moz-transition-duration: 1000ms;
    -moz-transition-timing-function: ease-out;
    -webkit-transform: rotate(360deg);
    -webkit-transition-duration: 1000ms;
    -webkit-transition-timing-function: ease-out;
    -o-transform: rotate(360deg);
    -o-transition-duration: 1000ms;
    -o-transition-timing-function: ease-out;
    transform: rotate(360deg);
    transition-duration: 1000ms;
    transition-timing-function: ease-out;
}

